@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", serif;
}

body {
  font-family: "Nunito", serif;
  background: #121212;
  color: #e0e0e0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

.app {
  width: 100%;
  max-width: 580px;
  background: #1f1f1f;
  border-radius: 2em;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.3);
  padding: 40px;
  text-align: center;
}

h1 {
  font-size: 1.8em;
  font-weight: 550;
  color: #fff;
  margin-bottom: 20px;
}

.subtitle {
  font-size: 0.95em;
  font-weight: 500;
  color: #989898;
  margin-bottom: 25px;
  line-height: 1.6;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* Обновления для радиокнопок */
.radios {
  display: flex;
  justify-content: center;
  gap: 0.8em;
}

.radio-label {
  display: flex;
  align-items: center;
  background: #2c2c2c;
  width: 100%;
  border-radius: 1.2em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-size: 0.9em;
  font-weight: 600;
  color: #e0e0e0;
  justify-content: center;
  border: 1px solid #3a3a3a;
}

.radio-label:hover {
  background-color: #333;
  transform: scale(1.05);
}
.radio-input {
  display: none; /* Скрываем стандартную радиокнопку */
}

.radio-label.selected {
  background: #007bff; /* Изменяем фон выбранной радиокнопки */
  color: #fff;
}

/* Стили для изображений иконок */
.img {
  width: 5em;
  height: 5em;
}

.radio-label input[type="radio"]:checked + .radio-label {
  background: #007bff; /* Фон при активном состоянии */
  color: #fff;
}

.input {
  width: 100%;
  padding: 12px 16px;
  margin-top: 1em;
  border: 1px solid #3a3a3a;
  border-radius: 0.8em;
  background: #2c2c2c;
  color: #e0e0e0;
  font-size: 16px;
  transition: 0.3s ease;
}

.input:focus {
  border-color: #007bff;
  background: #333;
  outline: none;
}

.error {
  color: #f44336;
  font-size: 14px;
  margin-bottom: 15px;
}

.pay-button {
  background: linear-gradient(45deg, #007bff, #0056b3);
  color: #fff;
  border: none;
  padding: 1em 3em;
  width: 100%;
  font-size: 0.95em;
  font-weight: 600;
  border-radius: 1em;
  cursor: pointer;
  transition: 0.3s ease;
  box-shadow: 0px 8px 18px rgba(0, 123, 255, 0.2);
  margin-top: 1em;
}

.pay-button:hover {
  background: linear-gradient(45deg, #0061c9, #004d95);
  transform: translateY(-3px);
  box-shadow: 0px 12px 25px rgba(0, 123, 255, 0.3);
}

.success-message {
  color: #e0e0e0;
  border-radius: 10px;
  transition: all 0.3s ease;
  max-width: 600px;
  margin: 0 auto;
}

.success-message h2 {
  font-size: 24px;
  font-weight: 600;
  color: #81c784; /* Зеленый для успеха */
  margin-bottom: 15px;
  text-align: center;
}
.price { 
  font-size: 1.3em;
  font-weight: 500;
}
.card{ 
  font-size: 1.8em;
  font-weight: 600;
  background-color: #121212;
  padding: 0.5em;
  border-radius: 0.6em;
}
.success-message p:last-child {
  margin-top: 20px;
  font-size: 14px;
  color: #888;
}

.success-message:hover {
  background-color: #333;
  box-shadow: 0px 6px 18px rgba(0, 123, 255, 0.3); /* Добавляем плавный эффект при наведении */
  border-color: #0061c9; /* Изменяем цвет обводки при наведении */
}

/* Стили для мобильных устройств */
@media (max-width: 600px) {
  .success-message {
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.3);
  }
}
